  //YanlÄ±Å MesajÄ±
  errorToastMessage(headerMessage, bodyMessage) {
    let type = NbToastStatus.DANGER;;
    this.showToast(type, headerMessage, bodyMessage);
  }

  //DoÄru MesajÄ±
